<template>

  <div style="margin-bottom:500px">
  <h4> Reset Password </h4>
  <hr>
    <div v-if="!submitted">
      <div class="form-group">
        <label for="username">Username</label>
        <input
          type="text"
          class="form-control"
          id="username"
          required
          v-model="courses.username"
          name="username"
        />
      </div>

      <div class="form-group">
        <label for="answer">Jawaban Reset Password</label>
        <input
        type="answer"
          class="form-control"
          id="answer"
          required
          v-model="courses.answer"
          name="answer"
        />
      </div>
      <br>
        <button @click="saveCourses" class="btn btn-success">Submit</button>
    </div>

    <div v-else>
      <h4>Password anda berhasil direset! Login kembali dengan password = username</h4>
      <button class="btn btn-success" @click="newCourses">Login</button>
    </div>
  </div>
       

</template>

<script>
import AutentikasiService from "../services/AutentikasiService";

export default {
  name: "lupa-password",
  data() {
    return {
      courses: {
        id: null,
        username: "",
        answer: "",
        published: false
      },
      submitted: false
    };
  },
  methods: {
    saveCourses() {
      var data = {
        username: this.courses.username,
        answer: this.courses.answer
      };

      AutentikasiService.resetPassword(data)
        .then(response => {
          this.courses.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
        })
        .catch(e => {
          console.log(e);
        });
    },
    
    newCourses() {
      this.submitted = false;
      this.courses = {};
    }
  }
};
</script>

<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>